.quote-container {
  align-items: center;
  background: linear-gradient(rgba(0, 0, 0, 0.87), rgba(0, 0, 0, 0.623)), url(/static/media/blog_post_7.d12a8e72f1ea633db82a.png);
  background-position: 100%;
  background-size: cover;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  min-height: calc(100vh - 360px);
  padding: 2rem;
  width: 100%
}
.quote-container h2{
  margin-bottom: 2rem;
  color: var(--white_clr);
}

.quote-wrapper{
  border: 0.1rem solid var(--primary);
  max-width: var(--space-640-960);
  padding: 1rem 2rem;
}

.quote-wrapper{
  border-left-width: 1rem;
}
.quote-wrapper::before{
  content: "\201c";
  font-size: 5rem;
  color: var(--primary);
  display: block;
  margin-bottom: -3.5rem;
  margin-top: -.5rem;
}

.quote-wrapper blockquote{
  border-left: none;
  border-bottom: .1rem solid white;
}
.quote_text{
  font-size: var(--fs-h4);
  color: var(--white_clr);
  border: none;
}
.quote_author {
  font-size: var(--fs-h5);
  font-weight: 700;
  font-style: normal;
  color: var(--primary);
  text-align: right;
  margin-top: 2rem;
}
.quote_author::before {
  content: "\2014\0020";
}
.button{
  display: flex;
  justify-content: center;
  align-items: center;
}
.quote_btn{
  background: #000000c2;
  border: .06rem solid var(--primary);
  border-radius: 2rem;
  color: var(--white_clr);
  cursor: pointer;
  font-size: var(--fs-h4);
  font-weight: 500;
  padding: .8rem 1rem;
  transition: all .7s ease-in-out;
  width: 40%;
  margin: 1rem 0;
}
.quote_btn:hover {
  background: var(--secondary)
}